<template>
  <v-flex xs4 class="elevation-1 pa-2 ma-4">
    <v-list two-line>
      
      <v-row class="pa-4 justify-space-between">
        
        <v-subheader>
          <h3>{{column}}</h3>
        </v-subheader>
      
        <v-dialog
          v-model="dialog"
          max-width="500px">
          
          <template v-slot:activator="{on, attrs}">
            <v-btn 
              rounded
              color="teal accent-4"
              class="white--text"
              v-bind="attrs"
              v-on="on">
              + Add Task
            </v-btn>
          </template>

          <v-card>
            <v-card-title class="text-h5">
              New Task
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-form 
                  ref="form"
                  @submit.prevent>
                  <v-text-field
                    outlined
                    label="Title"
                    v-model="taskTitle"
                    :rules="rules.required"/>
                  <v-text-field
                    outlined
                    label="Tags"
                    v-model="taskTags"
                    :rules="rules.required"/>
                  <v-text-field
                    outlined
                    v-model="taskAssignee"
                    label="Assignee"
                    :rules="rules.required"/>
                  <v-menu
                    ref="menuStart"
                    v-model="menuStart"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="taskStart"
                        label="Start date"
                        outlined
                        readonly
                        :rules="rules.required"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="taskStart"
                      @change="saveStart"
                    ></v-date-picker>
                  </v-menu>
                  <v-menu
                    ref="menuEnd"
                    v-model="menuEnd"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="taskEnd"
                        label="End date"
                        outlined
                        readonly
                        :rules="rules.required"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="taskEnd"
                      @change="saveEnd"
                    ></v-date-picker>
                  </v-menu>
                  <v-btn
                    color="primary"
                    @click="newTask"
                  >
                    Submit
                  </v-btn>
                </v-form>
              </v-container>
            </v-card-text>
          </v-card>    
        </v-dialog>

      </v-row>
      
      <draggable 
      v-model="tasks" 
      group="tasks" 
      style="min-height: 10px">
        
        <template v-for="item in tasks">
          <v-list-item 
            :key="item.title"
            class="elevation-2 pa-5"
            >
            <v-row class="flex-row justify-space-between">
              <v-col cols="12">
                <v-list-item-title>
                  <h3>{{item.title}}</h3>
                </v-list-item-title>
              </v-col>
              <v-col 
                cols="8"
                class="flex-grow-1">
                <v-avatar 
                  size="40px"
                  color="light-blue lighten-4">
                  {{item.assignee.slice(0,2)}}
                </v-avatar>
                <v-chip>
                  {{item.tags}}
                </v-chip>
              </v-col>
              <v-col 
                cols="4"
                class="flex-shrink-1">
                {{
                  Math.ceil((Date.parse(item.end_date) - Date.parse(item.start_date)) / 86400000)
                }} days
              </v-col>
            </v-row>
          </v-list-item>
        </template>

      </draggable>
    </v-list>
  </v-flex>
</template>

<script>
import draggable from 'vuedraggable'

export default {
    name: 'KanbanCard',
    components: {
        draggable,
    },
    data() {
      return {
        rules: {
          required: [value => !!value || "Required"]
        },
        taskTitle: '',
        taskTags: '',
        taskAssignee: '',
        taskStart: '',
        taskEnd: '',
        dialog: '',
        menuStart: '',
        menuEnd: ''
      }
    },
    props: {
      column: String,
      tasks: Array
    },
    methods: {
      saveStart (date) {
        this.$refs.menuStart.save(date)
      },
      saveEnd (date) {
        this.$refs.menuEnd.save(date)
      },
      newTask () {
        this.dialog = false

        let data = {
          issue_id: this.column,
          title: this.taskTitle,
          assignee: this.taskAssignee,
          start_date: this.taskStart,
          end_date: this.taskEnd,
          tags: this.taskTags
        }
        this.tasks.push(data)

        this.$refs.form.reset()
      }
    }
}
</script>