<template>
  <v-container fluid>
    <v-layout align-start justify-center>
      <KanbanCard 
        v-for="items in taskData"
        v-bind:key="items.column"
        v-bind:column="items.column"
        v-bind:tasks="items.tasks"
        />
    </v-layout>
  </v-container>
</template>

<script>
import taskData from '../json/data.json'
import KanbanCard from './KanbanCard'

var backlogArr = [];
var todoArr = [];
var doneArr = [];

for (let i in taskData) {
  if (taskData[i].issue_id == "Backlog") {
    backlogArr.push(taskData[i])
  } else if (taskData[i].issue_id == "To Do") {
    todoArr.push(taskData[i])
  } else {
    doneArr.push(taskData[i])
  }
}

export default {
    name: 'Kanban',
    components: {
        KanbanCard,
    },
    data() {
      return{
        taskData: [{
          column:'Backlog',
          tasks: backlogArr
        },
        {
          column: 'To Do',
          tasks: todoArr 
        },
        {
          column: 'Done',
          tasks: doneArr
        }]
      }
    },
}
</script>

